import {
  IonContent,
  IonIcon,
  IonItem,
  IonLabel,
  IonList,
  IonListHeader,
  IonMenu,
  IonMenuToggle,
  IonNote,
} from '@ionic/react';

import React from 'react';
import { useLocation } from 'react-router-dom';
import {
  peopleOutline,
  peopleSharp,
  carOutline, 
  carSharp, 
  callOutline, 
  callSharp, 
  walletOutline, 
  walletSharp,
  settingsOutline,
  settingsSharp
} from 'ionicons/icons';
import './Menu.css';

interface AppPage {
  url: string;
  iosIcon: string;
  mdIcon: string;
  title: string;
}

const appPages: AppPage[] = [
  {
    title: 'Amigos',
    url: '/page/amigos',
    iosIcon: peopleOutline,
    mdIcon: peopleSharp
  },
  {
    title: 'Alcanzale',
    url: '/page/alcanzale',
    iosIcon: carOutline,
    mdIcon: carSharp
  },
  {
    title: 'Alcanzame',
    url: '/page/alcanzame',
    iosIcon: callOutline,
    mdIcon: callSharp
  },
  {
    title: 'Mi billletera',
    url: '/page/miBilletera',
    iosIcon: walletOutline,
    mdIcon: walletSharp
  },
  {
    title: 'Mi cuenta',
    url: '/page/miCuenta',
    iosIcon: settingsOutline,
    mdIcon: settingsSharp
  }
];

const Menu: React.FC = () => {
  const location = useLocation();

  return (
    <IonMenu contentId="main" type="overlay">
      <IonContent>
        <IonList id="inbox-list">
          <IonListHeader>Alcanzame</IonListHeader>
          <IonNote>Bienvenido @chelo</IonNote>
          {appPages.map((appPage, index) => {
            return (
              <IonMenuToggle key={index} autoHide={false}>
                <IonItem className={location.pathname === appPage.url ? 'selected' : ''} routerLink={appPage.url} routerDirection="none" lines="none" detail={false}>
                  <IonIcon slot="start" ios={appPage.iosIcon} md={appPage.mdIcon} />
                  <IonLabel>{appPage.title}</IonLabel>
                </IonItem>
              </IonMenuToggle>
            );
          })}
        </IonList>
      </IonContent>
    </IonMenu>
  );
};

export default Menu;
