import React from 'react';
import './ExploreContainer.css';
import AmigosList from './amigos/AmigosList';
import AlcanzaleList from './alcanzale/AlcanzaleList';
import AlcanzameList from './alcanzame/AlcanzameList';
import Billetera from './billetera/MiBilletera';

interface ContainerProps {
  name: string;
}

const ExploreContainer: React.FC<ContainerProps> = ({ name }) => {
  return (
    
      <div className="container">
        {name === "amigos" &&
          <AmigosList name="Mis Amigos"/>
        }
        {name === "alcanzale" &&
          <AlcanzaleList name="Alcanzale"/>
        }
        {name === "alcanzame" &&
          <AlcanzameList name="Alcanzame"/>
        }
        {name === "miBilletera" &&
          <Billetera name="Billetera"/>
        }
      </div >
  );
};

export default ExploreContainer;
