import React from 'react';
import '../ExploreContainer.css';
import { IonList, IonItem, IonListHeader } from '@ionic/react';
import {
  peopleOutline,
  peopleSharp,
} from 'ionicons/icons';

import {
  IonIcon,
  IonLabel
} from '@ionic/react';

interface ContainerProps {
  name: string;
}

interface Friends {
  nombre: string;
  apellido: string;
}

const friends: Friends[] = [
  {
    nombre: 'Juan',
    apellido: 'De los Palotes'
  },
  {
    nombre: 'Leonardo',
    apellido: 'Garcia'
  },
  {
    nombre: 'Soledad',
    apellido: 'Palma'
  }
];

const AmigosList: React.FC<ContainerProps> = ({ name }) => {
  return (
    <div className="container">
      <IonList>
          <IonListHeader>{name}</IonListHeader>
          {friends.map((friend, index) => {
            return (
                <IonItem>
                  <IonIcon slot="start" ios={peopleOutline} md={peopleSharp} />
                  <IonLabel>{friend.nombre}</IonLabel>
                  <IonLabel>{friend.apellido}</IonLabel>
                </IonItem>
            );
          })}
      </IonList>
    </div>
  );
};

export default AmigosList;
